<template lang="pug">
  
  .enter-phone.calls-fixed-height
    .current-phone
      input(type='text' @input='typeNum(false)' v-model.trim='phoneNumber').current-phone__number
      router-link(
        :to="{name: 'CreateContact', params: { options: {type: 'create', data: {number: phoneNumber}}} }"
        :class='{"current-phone__add_hidden": !phoneNumber.length}'
        tag='button'
      ).current-phone__add Добавить номер

    .phone-buttons
      .phone-buttons__item(v-for='number in buttons' @click='typeNum(number.digit)')
        h1.phone-buttons__num {{number.digit}}
        p.phone-buttons__chars {{number.chars}}
      .phone-buttons__item.phone-buttons__item_empty
      .phone-buttons__item.phone-buttons__item_call(@click='call')
        svg
          use(xlink:href='#call')
      .phone-buttons__item.phone-buttons__item_empty(@click='deleteSymbol')
        img(src='../../assets/img/icons/backspace.svg')



</template>

<script>

  export default {
    data() {
      return {
        phoneNumber: '',
        buttons: [
          {digit: 1},
          {digit: 2, chars: 'абвг'},
          {digit: 3, chars: 'дежз'},
          {digit: 4, chars: 'ийкл'},
          {digit: 5, chars: 'мноп'},
          {digit: 6, chars: 'рсту'},
          {digit: 7, chars: 'фхцч'},
          {digit: 8, chars: 'шщъы'},
          {digit: 9, chars: 'ьюэя'},
          {digit: '*'},
          {digit: 0},
          {digit: '#'},
        ]
      }
    },
    methods: {
      typeNum(num) {
        this.phoneNumber = this.phoneNumber.replace(/[^\d\W]/,'');
        num !== false ? this.phoneNumber += num : null
      },
      call(){
        this.phoneNumber.length !== 6 
          ? this.$alert.show('Введите номер корректно')
          : (
            this.$store.state.calls.blocked.includes(parseInt(this.phoneNumber))
              ? this.$alert.show('Номер в черном списке')
              : this.$store.dispatch('MAKE_CALL', {interlocutor: this.phoneNumber, type: 'outward'})
          )
      },
      deleteSymbol() {
        this.phoneNumber ? this.phoneNumber = this.phoneNumber.slice(0,-1) : null
      }
    }
  }

</script>

<style lang="sass"> @import '../../assets/sass/phone/apps/calls/enter' </style>